// Component for the dropdown

import React from "react";

class PresenceDropdown extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      current: this.props.current, // the plug load that the dropdown is for
      selected: "", // selected value
      disabled: this.props.disabled, // disable dropdown (true or false)
      id: this.props.current + "select", // id given to component
      plugLoadExtra: "", // additional string to help with task lamp formatting
    };
    window.dropdown = this; // make sure that dropdown can be accessed globally
  }
  render() {
      // if the plug load is task lamp, format from TaskLamp to Task Lamp
    if (this.state.current === "TaskLamp") {
      this.state.plugLoadExtra = "Task Lamp";
    } else {
      this.state.plugLoadExtra = this.state.current;
    }
    return (
      <>
        <h3 style={{fontFamily: "Helvetica", fontSize: "12px", marginTop: "10px"}}> {this.state.plugLoadExtra} </h3>
        <select
          style={{margin: "10px 0"}}
          defaultValue={this.state.selected}
          name={this.state.current}
          id={this.state.id}
          onChange={e =>
            this.setState({
              selected: e.target.value
            })
          }
          disabled={this.state.disabled}
        >
          <option value="5 minutes"> 5 minutes </option>
          <option value="10 minutes"> 10 minutes </option>
          <option value="20 minutes"> 20 minutes </option>
          <option value="30 minutes"> 30 minutes </option>
          <option value="60 minutes"> 1 hour </option>
          <option value="other"> Other </option>
        </select>
      </>
    );
  }
}

export default PresenceDropdown;
